        </b-collapse>
      </div>
    </b-navbar>
    <div class="section-header-padding-top"></div>
    <h1 class="text-center section-header">Company Profile</h1>
    <div class="section-header-underline"></div>
    <div class="section-header-padding-bottom"></div>
    <div v-if="!showEditForm" class="row section-content-container">
      <div class="col-md-12 col-sm-12 col-xs-12">
        <h3>New Employee</h3>
        <b-form>
          <b-form-group>
            <b-form-input
              type="text"
              v-model="firstName"
              placeholder="First Name"
            ></b-form-input>
          </b-form-group>  
          <b-form-group>
            <b-form-input
              type="text"
              v-model="lastName"
              placeholder="Last Name"
            ></b-form-input>
          </b-form-group>    
          <b-form-group>
            <b-form-input
              type="text"
              v-model="title"
              placeholder="Title"
            ></b-form-input>
          </b-form-group>          
          <b-form-group>
            <b-form-textarea
              v-model="bio"
              rows="3"
              placeholder="Bio"
            ></b-form-textarea>
          </b-form-group>   
          <b-form-group>
            <b-form-textarea
              v-model="quote"
