              placeholder="Quote"
            ></b-form-textarea>
          </b-form-group>
          Profile Photo
          <b-form-group>
            <b-form-file
              placeholder="Choose a file..."
              @change="onFileSelected"
              class="file-upload"
            ></b-form-file>
          </b-form-group>   
          <b-form-group>
