              v-model="bulletPoints"
              rows="3"
              placeholder="Vessel(s) *separate with comma"
            ></b-form-textarea>
          </b-form-group>   
          <b-button type="submit" @click.prevent="addEmployee()" variant="primary">Submit</b-button>
        </b-form>
      </div>
