              <div class="col-md-12" v-for="bullet in employee.bullet_points" v-bind:key="bullet._id">
                <ul>
                  <li>{{bullet}}</li>
                </ul>
              </div>
            </div>
          </div>
          <div class="form-padding-bottom" v-if="showEditForm && employee._id === selectedEmployeeId">
            <b-form>
              First Name
              <b-form-group>
                <b-form-input
                  type="text"
                  v-model="updatedFirstName"
                  placeholder="First Name"
                ></b-form-input>
              </b-form-group>  
              Last Name
              <b-form-group>
                <b-form-input
                  type="text"
                  v-model="updatedLastName"
                  placeholder="Last Name"
                ></b-form-input>
              </b-form-group>
              Title    
              <b-form-group>
                <b-form-input
                  type="text"
                  v-model="updatedTitle"
                  placeholder="Title"
                ></b-form-input>
              </b-form-group>
              Bio          
              <b-form-group>
                <b-form-textarea
                  v-model="updatedBio"
                  rows="3"
                  placeholder="Bio"
                ></b-form-textarea>
              </b-form-group>   
              Quote
              <b-form-group>
                <b-form-textarea
                  v-model="updatedQuote"
                  rows="3"
                  placeholder="Quote"
                ></b-form-textarea>
              </b-form-group>   
              Profile Photo    
              <b-form-group>
                <b-form-file
                  placeholder="Choose a file..."
                  @change="onFileSelected"
                  class="file-upload"
                ></b-form-file>
              </b-form-group>   
              Vessel(s) *separate with comma
              <b-form-group>
                <b-form-textarea
                  v-model="updatedBulletPoints"
                  rows="3"
                  placeholder="Vessel(s) *separate with comma"
                ></b-form-textarea>
              </b-form-group>   
              <b-form>
              <b-button type="submit" @click.prevent="updateEmployee(updatedFirstName, updatedLastName, updatedTitle, updatedBio, updatedQuote, selectedImage, updatedBulletPoints, `${employee._id}`)" variant="primary">Submit</b-button>
              <b-button @click.prevent="cancel()">Cancel</b-button>
            </b-form>
            </b-form>
          </div>
          <div class="blog-actions">
            <font-awesome-icon @click.prevent="editEmployee(`${employee._id}`)" class="blog-padding-edit" :icon="{ prefix: 'fas', iconName: 'edit' }" />
            <font-awesome-icon @click.prevent="deleteEmployee(`${employee._id}`)" class="blog-delete" :icon="{ prefix: 'fas', iconName: 'minus-circle' }" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import EmployeeService from '../services/EmployeeService';

export default {
  name: 'EditCompanyProfile',
  data () {
    return {
      employees: [],
      firstName: "",
      lastName: "",
      title: "",
      bio: "",
      quote: "",
      selectedImage: "",
      bulletPoints: "",
      showEditForm: false,
      updatedFirstName: "",
      updatedLastName: "",
      updatedTitle: "",
      updatedBio: "",
      updatedQuote: "",
      updatedPhotoFile: "",
      updatedBulletPoints: "",
      selectedEmployeeId: ""
    }
	},
	methods: {
    logout() {
			localStorage.clear();
			this.$router.go('/admin');
    },
    getEmployees() {
      let that = this;
      axios.get("/api/employees/").then(function(res) {
        let data = res.data.employees
        function compare(a, b) {
          if (a.createdAt > b.createdAt)
            return 1;
          if (a.createdAt < b.createdAt)
            return -1;
          return 0;
        }   
        that.employees = data.sort(compare); 
      }).catch(function(err) {
        console.log(err)
      });
    },
    onFileSelected(event) {
      this.selectedImage = event.target.files[0];
      const files = event.target.files;
      if (files && files[0]) {
        const reader = new FileReader;
        reader.onload = e => {
          this.imageData = e.target.result;
        }
        reader.readAsDataURL(files[0]);
      }
    },
    addEmployee() {
      let that = this;
      EmployeeService.addEmployee(this.firstName, this.lastName, this.title, this.bio, this.quote, this.selectedImage, this.bulletPoints).then(function() {
        window.location.href = '/edit-company-profile';
      });
    },
    editEmployee(id) {
      let that = this;
      this.showEditForm = true;
      axios.get("/api/employees/" + id).then(function(res) {
        let data = res.data.employee;
        that.updatedFirstName = data.first_name;
        that.updatedLastName = data.last_name;
        that.updatedTitle = data.title;
        that.updatedBio = data.bio;
        that.updatedQuote = data.quote;
        that.updatedBulletPoints = data.bullet_points.toString();
        that.selectedEmployeeId = data._id;
        return data;
      }).catch(function(err) {
        return err;
      });
    },
    updateEmployee(firstName, lastName, title, bio, quote, photo, bulletPoints, id) {
      let that = this;
      let formData = new FormData();
      formData.append("first_name", firstName);
      formData.append("last_name", lastName);
      formData.append("title", title);
      formData.append("bio", bio);
      formData.append("quote", quote);
      formData.append("photo", photo);
      formData.append("bullets", bulletPoints);

      axios.put("/api/employees/" + id, formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }).then(function(data) {
        that.getEmployees();
        that.cancel();
        return data;
      }).catch(function(err) {
        return err;
      });
    },
    cancel() {
      this.showEditForm = false;
    },
    deleteEmployee(id) {
      let that = this;
      EmployeeService.deleteEmployee(id).then(function() {
        that.getEmployees();
      });
    }
  },
  mounted () {
    this.getEmployees();
  }
}
</script>